table {
	border-collapse: collapse;
	border-style: hidden;
	border-radius: 1em;
	/* box-shadow: 0 0 0 1px #616161; */
	box-shadow: 0 0 10px rgba(0, 0, 0, 0.5) !important;
}

/* thead th {
	font-weight: normal;
	color: white;
	background-color: #616161;
} */
/* tr:nth-child(even) td {
	background-color: rgba(144, 164, 174, 0.05);
} */

tr:nth-child(3n):not(:last-child) td {
	border-bottom: 2px solid black;
}

tr td:nth-child(3n):not(:last-child) {
	border-right: 2px solid black;
}

td {
	border: 1px solid #9e9e9e;
	padding: 0.75em;
	line-height: 2em;
	width: 3.5em;
	text-align: center;
}

tr:first-child td:first-child {
	border-top-left-radius: 1em;
}
tr:first-child td:last-child {
	border-top-right-radius: 1em;
}
tr:last-child td:first-child {
	border-bottom-left-radius: 1em;
}
tr:last-child td:last-child {
	border-bottom-right-radius: 1em;
}

.possible-values {
	position: absolute;
	top: -8px;
	left: 4px;
	z-index: 1;
	font-size: 8px;
	font-weight: normal;
	color: #90a4ae;
	letter-spacing: 1px;
}

.config {
	display: inline-block;
	margin: 1em;
	vertical-align: top;
	font-size: 0.85em;
	color: #90a4ae;
	line-height: 1.25;
}

#digits-table.answer-mode td {
	color: #1565c0;
	font-weight: normal;
}
